import React from 'react';
import { useNavigate } from 'react-router-dom'

const PersonalSeller = () => {
  //   -    url
  let navigate = useNavigate()

  const onSubmit = () => {
    localStorage.clear();
    navigate('/authorization')
  }

  return (
    <div>
           
      <button onClick={onSubmit}></button>
    </div>
  )
}

export default PersonalSeller